:root {
  --lx-base-size: 100%;
  --lx-gap: 1.125em;
  --lx-radius-01: 0.375rem;
  --lx-radius-02: 0.75rem;
  --lx-radius-03: 1.125rem;
  --lx-error: hsl(354, 100%, 59%);
  --lx-info: hsl(205, 100%, 45%);
  --lx-success: hsl(110, 100%, 35%);
  --lx-warning: hsl(29, 100%, 55%);
  --lx-neutral-01: hsl(0, 0%, 96%);
  --lx-neutral-02: hsl(0, 0%, 72%);
  --lx-neutral-03: hsl(0, 0%, 48%);
  --lx-neutral-04: hsl(0, 0%, 36%);
  --lx-neutral-05: hsl(0, 0%, 24%);
  --lx-neutral-06: hsl(0, 0%, 12%);
}

@supports (font-size: clamp(1rem, 1vw, 1rem)) {
  :root {
    --lx-text-01: clamp(0.8rem, 0.2vw + 0.7rem, 0.9rem);
    --lx-text-02: clamp(0.9rem, 0.3vw + 0.8rem, 1.1rem);
    --lx-text-03: clamp(1.1rem, 0.5vw + 1rem, 1.4rem);
    --lx-text-04: clamp(1.3rem, 0.7vw + 1.2rem, 1.7rem);
    --lx-text-05: clamp(1.6rem, 1vw + 1.4rem, 2.1rem);
    --lx-text-06: clamp(1.9rem, 1.3vw + 1.6rem, 2.7rem);
  }
}
@supports not (font-size: clamp(1rem, 1vw, 1rem)) {
  :root {
    --lx-text-01: 0.8rem;
    --lx-text-02: 0.9rem;
    --lx-text-03: 1.1rem;
    --lx-text-04: 1.3rem;
    --lx-text-05: 1.6rem;
    --lx-text-06: 1.9rem;
  }
  @media screen and (min-width: 75rem) {
    :root {
      --lx-text-01: 0.9rem;
      --lx-text-02: 1.1rem;
      --lx-text-03: 1.4rem;
      --lx-text-04: 1.7rem;
      --lx-text-05: 2.1rem;
      --lx-text-06: 2.7rem;
    }
  }
}
:root {
  --lx-space-01: clamp(0.5625rem, 0.4073rem + 0.7759vw, 1.125rem);
  --lx-space-02: clamp(0.75rem, 0.5431rem + 1.0345vw, 1.5rem);
  --lx-space-03: clamp(1.125rem, 0.8147rem + 1.5517vw, 2.25rem);
  --lx-space-04: clamp(1.5rem, 1.0862rem + 2.069vw, 3rem);
  --lx-space-05: clamp(2.25rem, 1.6293rem + 3.1034vw, 4.5rem);
  --lx-space-06: clamp(3rem, 2.1724rem + 4.1379vw, 6rem);
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}

html {
  text-size-adjust: 100%;
  font-size: var(--base-size);
  font-variation-settings: normal;
}

html,
body {
  margin: 0;
  padding: 0;
  word-break: break-word;
}

body {
  line-height: 1.8;
  font-size: var(--lx-text-02);
}

header {
  z-index: 2;
}

main,
footer {
  z-index: 1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 700;
}

h1 {
  font-size: var(--lx-text-06);
}

h2 {
  font-size: var(--lx-text-05);
}

h3 {
  font-size: var(--lx-text-04);
}

h4 {
  font-size: var(--lx-text-03);
}

h5 {
  font-size: var(--lx-text-02);
}

h6 {
  font-size: var(--lx-text-02);
}

p {
  margin: 0;
}

b,
strong {
  font-weight: 700;
}

small {
  font-size: var(--lx-text-sm);
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
}

span {
  display: inline-block;
}

figure,
article {
  margin: 0;
  padding: 0;
  border-style: none;
}

img {
  max-width: 100%;
  max-height: 100%;
  border-style: none;
}

hr {
  width: 100%;
  height: 0.125rem;
  margin: 0;
  padding: 0;
  position: relative;
  display: block;
  border: none;
}

.lx-container-50 {
  width: 50%;
  max-width: 60rem;
  min-width: 10rem;
  margin: 0 auto;
}
.lx-container-55 {
  width: 55%;
  max-width: 66rem;
  min-width: 11rem;
  margin: 0 auto;
}
.lx-container-60 {
  width: 60%;
  max-width: 72rem;
  min-width: 12rem;
  margin: 0 auto;
}
.lx-container-65 {
  width: 65%;
  max-width: 78rem;
  min-width: 13rem;
  margin: 0 auto;
}
.lx-container-70 {
  width: 70%;
  max-width: 84rem;
  min-width: 14rem;
  margin: 0 auto;
}
.lx-container-75 {
  width: 75%;
  max-width: 90rem;
  min-width: 15rem;
  margin: 0 auto;
}
.lx-container-80 {
  width: 80%;
  max-width: 96rem;
  min-width: 16rem;
  margin: 0 auto;
}
.lx-container-85 {
  width: 85%;
  max-width: 102rem;
  min-width: 17rem;
  margin: 0 auto;
}
.lx-container-90 {
  width: 90%;
  max-width: 108rem;
  min-width: 18rem;
  margin: 0 auto;
}
.lx-container-95 {
  width: 95%;
  max-width: 114rem;
  min-width: 19rem;
  margin: 0 auto;
}
.lx-container-100 {
  width: 100%;
  max-width: 120rem;
  min-width: 20rem;
  margin: 0 auto;
}

.lx-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--lx-gap);
}
.lx-row.is-gapless {
  gap: 0;
}

.lx-col {
  display: block;
  flex: auto;
}
.lx-col.is-1 {
  flex: none;
  width: 8.3333333333%;
}
.lx-col.is-2 {
  flex: none;
  width: 16.6666666667%;
}
.lx-col.is-3 {
  flex: none;
  width: 25%;
}
.lx-col.is-4 {
  flex: none;
  width: 33.3333333333%;
}
.lx-col.is-5 {
  flex: none;
  width: 41.6666666667%;
}
.lx-col.is-6 {
  flex: none;
  width: 50%;
}
.lx-col.is-7 {
  flex: none;
  width: 58.3333333333%;
}
.lx-col.is-8 {
  flex: none;
  width: 66.6666666667%;
}
.lx-col.is-9 {
  flex: none;
  width: 75%;
}
.lx-col.is-10 {
  flex: none;
  width: 83.3333333333%;
}
.lx-col.is-11 {
  flex: none;
  width: 91.6666666667%;
}
.lx-col.is-12 {
  flex: none;
  width: 100%;
}

@media screen and (max-width: 75rem) {
  .lx-container-50 {
    max-width: 24rem;
  }
  .lx-container-55 {
    max-width: 26.4rem;
  }
  .lx-container-60 {
    max-width: 28.8rem;
  }
  .lx-container-65 {
    max-width: 31.2rem;
  }
  .lx-container-70 {
    max-width: 33.6rem;
  }
  .lx-container-75 {
    max-width: 36rem;
  }
  .lx-container-80 {
    max-width: 38.4rem;
  }
  .lx-container-85 {
    max-width: 40.8rem;
  }
  .lx-container-90 {
    max-width: 43.2rem;
  }
  .lx-container-95 {
    max-width: 45.6rem;
  }
  .lx-container-100 {
    max-width: 48rem;
  }
  .lx-row {
    flex-direction: column;
  }
  .lx-col {
    width: 100%;
  }
}
.hide,
.hide-on-desk {
  display: none;
  visibility: hidden;
}

.has-dflex {
  display: flex;
}
.has-dflex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flow-column-wrap {
  flex-direction: column;
  flex-wrap: wrap;
}
.flow-column-nowrap {
  flex-direction: column;
  flex-wrap: nowrap;
}
.flow-row-wrap {
  flex-direction: row;
  flex-wrap: wrap;
}
.flow-row-nowrap {
  flex-direction: row;
  flex-wrap: nowrap;
}

.align-center {
  align-items: center;
}
.align-end {
  align-items: flex-end;
}
.align-start {
  align-items: flex-start;
}
.align-stretch {
  align-items: stretch;
}

.justify-around {
  justify-content: space-around;
}
.justify-between {
  justify-content: space-between;
}
.justify-evenly {
  justify-content: space-evenly;
}
.justify-center {
  justify-content: center;
}
.justify-end {
  justify-content: flex-end;
}
.justify-start {
  justify-content: flex-start;
}

.is-gapless {
  gap: 0;
}

@media screen and (max-width: 75rem) {
  .hide-on-desk {
    display: block;
    visibility: visible;
  }
  .hide-on-mb {
    display: none;
    visibility: hidden;
  }
}